@media (max-width: 600px){
    div.post  div.post-info{
      display: block !important;
      margin-top: 10px;
      
    }
    div.post  div.post-info  span{
     font-size: 12px !important;
    }

    div.post  p{
      font-size: 18px !important;
    }
    div.edit-and-create-post  textarea{
      font-size: 18px !important;
    }
 }


 body{
   margin: 0px;
   background-color: lightgrey;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif !important;
 }
 div#top-bar{
   background-color: darkgray;
   width: 100%;
   text-align: center;
   font-size: 30px;
   line-height: 2.9;

 }

 div.post{
   background-color: teal;
   max-width: 1000px;
   padding: 20px;
   margin: 20px auto;
   border-radius: 10px;
   color: ivory;
   box-shadow: 0px 0px 7px black;
 }

 div.post  h2{
   margin-top: 0px;
   color: black;
 }

 div.post  p {
   font-size: 21px;
 }

 div.post  button{
   border: 0px;
   font-size: 18px;
   border-radius: 5px;
   padding: 6px;
   border: 1px solid black ;
   align-items: center;
   justify-content: center;
   position: relative;
   transition: box-shadow 0.6s ease-in-out;;
   margin-right: 5px;
 }

 div.post  button:focus{
   outline: none;
 }

 div.post  button:hover{
   cursor: pointer;
   box-shadow: 0px 0px 8px black;
 }

 div.post  button.like-btn{
   background-color: limegreen;
 }

 div.post  button.unlike-btn{
   background-color: crimson;
   color: ivory;
 }
 div.post  button.unlike-btn  img.heart-crossed{
   filter: invert(100%);
 }
 div.post  button.unlike-btn  img.x-img{
   top: 7px !important;
 }

 div.post  button.edit-btn{
   background-color: lightskyblue;
 }

 div.post  button.delete-btn{
   background-color: red;
   color: ivory;
 }
 div.post  button.publish-btn{
   background-color: pink;
 }
 div.post  button.change-btn{
   background-color: lime;
 }
 div.post  button  img{
   position: absolute;
   top: 8px;
   left: 2px;
 }
 div.post  button.delete-btn  img{
   filter: invert(100%);
 }

 div.post  div.post-info{
   display: inline-block;
 }

 div.post  div.post-info  span{
   color: black;
   font-size: 18px;
 }

 div.post  div.post-info  span.footer-posted-at{
   padding-left: 15px;
   font-style: italic;
 }
 div.post  div.post-info  span.like-number{
   padding-left: 10px;
   font-weight: bolder;
   text-decoration: underline;
 }
 div.post  div.post-info  span.date{
   font-style: italic;
   text-decoration: underline;
 }

 div.edit-and-create-post  textarea{
   width: 99%;
   height: 150px;
   margin-bottom: 20px;
   resize: none;
   border-radius: 8px;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   font-size: 21px;
   background-color: ivory;
   transition: box-shadow 0.6s ease-in-out;;

 }
 div.edit-and-create-post  textarea:focus{
  outline: none; 
  box-shadow: 0px 0px 8px black;
 }